$(function() {
    /* Top Menu class Toggle */

    if ($(window).scrollTop() >= 80) {
        $('.top').addClass('fixed');
    }

    $(window).scroll(function(){
        if ($(window).scrollTop() >= 80) {
            $('.top').addClass('fixed');
        }
        else {
            $('.top').removeClass('fixed');
        }
    });

});



