<template>
    <div>


        <section id="design">
            <div class="wr">
                <ul id="design_list">
                    <li v-for="element in $store.state.elements" v-in-viewport.once>
                        <i class="icon" v-bind:class="[element.icon]"></i>
                        <h3>{{element.title}}</h3>
                        <p>{{element.text}}</p>
                    </li>

                </ul>
            </div>
        </section>


    </div>
</template>

<script>
    export default {
        name: "design"
    }




</script>

<style scoped lang="scss">
    @import "../../assets/style/settings";
    #design {
        &>.wr {
            padding-top: 100px;
            padding-bottom: 50px;

            &>ul {
                display: flex;
                flex-wrap: wrap;
                flex-direction: row;
                justify-content: center;
                align-items: center;
                align-content: center;

                &>li {
                    width: 260px;
                    margin: 0 10px;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;
                    opacity: 0;
                    transition: opacity 1.3s;


                    &>i {
                        color:$brand_orange;
                        display: block;
                        width: 150px;
                        height: 150px;
                        font-size:60px;
                        border: 1px solid #c5c7ca;
                        text-align: center;
                        padding-top: 35px;
                        box-sizing: border-box;
                        margin-bottom: 30px;
                    }
                    &>h3 {
                        font-weight: 600;
                        color:$brand_grey;
                        margin: 0;
                        margin-bottom: 25px;
                        padding: 0;
                    }
                    &>p {
                        text-align: center;
                        color: $brand_grey;
                        font-size: 14px;
                        line-height: 26px;
                        max-width: 180px;
                        margin: 0;
                        margin-bottom: 40px;
                        padding: 0;
                    }
                }
                &>li.in-viewport {
                    opacity: 1;
                }
            }
        }
    }

</style>


