<template>
    <div>

        <section id="comments">
            <div class="wr">
                <div>
                    <img src="../../assets/images/Name_1.jpg" width="129" height="138" alt="">
                    <p>These guys are incredible! I get my project in 10 days and it was awesome!
                        Very good service! Highly recommended!</p>
                    <p>John Smith</p>
                </div>
            </div>
        </section>


    </div>
</template>

<script>
    export default {
        name: "comments"
    }
</script>

<style scoped lang="scss">
    #comments {
        background: url("../../assets/images/woman_bg.jpg") no-repeat center fixed;
        background-size: cover;
        height: 500px;
        width: 100%;

        &>.wr {
            display: flex;
            flex-direction: row;
            justify-content: center;
            flex-wrap: wrap;
            padding-top: 100px;
            padding-bottom: 100px;

            &>div {
                height: 300px;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                align-content: center;

                &>img {
                    border-radius: 50%;
                }
                &>p {
                    color: white;
                }

                &>p:nth-of-type(1) {

                    font-size: 20px;
                    font-style: italic;
                    text-align: center;
                    color: #ffffff;
                    max-width: 800px;
                }
            }
        }
    }
</style>


