<template>
    <div>

        <section id="hello">
            <div class="wr">
                <h2>SAY <span class="orange_color">HELLO</span></h2>
                <p>Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum</p>

                <form action="">
                    <div class="row-1">

                        <input type="email" placeholder="Your e-mail">
                        <input type="text" placeholder="Subject">
                    </div>
                    <div class="row-2">
                        <textarea name="" id="" placeholder="Your Message"></textarea>
                    </div>
                    <div class="row-3">

                        <button>HIRE US</button>
                        <span class="hello_icons">
                            <a href=""><i class="fab fa-facebook-f"></i></a>
                            <a href=""><i class="fab fa-twitter"></i></a>
                            <a href=""><i class="fab fa-google-plus-g"></i></a>
                            <a href=""><i class="fab fa-dribbble"></i></a>
                            <a href=""><i class="fab fa-behance"></i></a>

                    </span>
                    </div>
                </form>

            </div>
        </section>

    </div>
</template>

<script>
    export default {
        name: "hello"
    }
</script>

<style scoped lang="scss">
@import "../../assets/style/settings";

#hello {
    background: url("../../assets/images/city_bg.jpg") no-repeat center fixed;
    background-size: cover;

    &>.wr {
        padding-top: 100px;
        padding-bottom: 100px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        &>h2 {
            color: #ffffff;
        }
        &>p {
            color: #ffffff;
            font-weight: 400;
            max-width: 450px;
            text-align: center;
        }
        &>form {
            max-width: 830px;
            display: flex;
            justify-content: center;
            align-items: center;
            flex-wrap: wrap;
            &>.row-1 {
                display: flex;
                width: 100%;
                justify-content: space-between;
                flex-wrap: wrap;
                &>input {
                    @include media($form){
                        width: 100%;
                    }
                    width: 400px;
                    height: 50px;
                    background-color: white;
                    border-radius: 10px;
                    outline: none;
                    border: none;
                    padding: 15px;
                    margin-bottom: 10px;
                    box-sizing: border-box;


                }
            }

            &>.row-2 {
                display: flex;
                width: 100%;
                justify-content: center;
                flex-wrap: wrap;
                & > textarea {
                    width: 100%;
                    height: 200px;
                    background-color: white;
                    border-radius: 10px;
                    padding: 10px;
                    margin: 20px 0;
                    outline: none;
                    border: none;


                }
            }

            &>.row-3 {
                display: flex;
                width: 100%;
                justify-content: space-between;
                flex-wrap: wrap;
                align-items: center;

                &>button {
                    margin-bottom: 15px;

                }
                &>.hello_icons {

                    &>a {
                        margin-left: 10px;
                        &:first-of-type {
                            margin-left: 0;
                        }
                        &>i {
                            width: 40px;
                            height: 40px;
                            font-size: 20px;
                            text-align: center;
                            padding-top: 10px;
                            box-sizing: border-box;

                        }
                    }
                }
            }

        }
    }
}


</style>


