<template>
<div>


    <header id="home">
        <div class="wr">
            <scrollactive class="my-nav"
                          active-class="active"
                          :offset="80"
                          :duration="800"
                          bezier-easing-value=".5,0,.35,1"
            >
            <nav  v-if="display">
                <a href="#home" class="scrollactive-item" @click.prevent="display = !display">HOME</a>
                <a href="#team" class="scrollactive-item" @click.prevent="display = !display">ABOUT US</a>
                <a href="#design" class="scrollactive-item" @click.prevent="display = !display">SERVICES</a>
                <a href="#works" class="scrollactive-item" @click.prevent="display = !display">OUR WORKS</a>
                <a href="#hello" class="scrollactive-item" @click.prevent="display = !display">CONTACT US</a>
                <hr />
                <span>
    <a href=""><i class="fab fa-facebook-f"></i></a>
    <a href=""><i class="fab fa-twitter"></i></a>
    <a href=""><i class="fab fa-google-plus-g"></i></a>
    <a href=""><i class="fab fa-dribbble"></i></a>
    <a href=""><i class="fab fa-behance"></i></a>
   </span>
                <a href="#" @click.prevent="display = !display"><i class="fas fa-times"></i></a>

            </nav>
            </scrollactive>

            <div class="top">
                <div class="content">


                    <a href=""><img src="images/Logo.png" width="158" height="18" alt=""></a>
                    <a href="#" @click.prevent="display = !display">Menu <i class="fas fa-bars"></i></a>

                </div>
            </div>

            <h1>WE DESIGN <span class="orange_color">THINGS</span></h1>
            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit,<br>
                sed do eiusmod tempor incididunt ut labore et dolore magna aliqua</p>
            <div class="buttons">
                <button>HIRE US</button>
                <button>OUR WORKS</button>
            </div>



        </div>
    </header>





</div>
</template>

<script>
    export default {
        name: "header",
        data () {
            return {
                display: false
            }
        }
    }
</script>

<style scoped lang="scss">
    @import "../../assets/style/settings";
    header {
        height: 800px;
        width: 100%;
        background: url("../../assets/images/macbook_bg.jpg") no-repeat center fixed;
        background-size: cover;

        &>.wr {
            padding-top: 150px;
            display: flex;
            flex-direction: column;
            justify-content: flex-start;
            align-items: center;
            &>.top {
                position: fixed;
                z-index:98;
                top: 0;
                width: 100%;
                height: 80px;
                margin-bottom: 100px;
                transition: all 0.2s ease;


                &>.content {
                    margin: 25px auto;
                    display: flex;
                    justify-content: space-between;
                    max-width: $normal;
                    padding: 0 15px;
                    box-sizing: border-box;


                    &>a:nth-of-type(2) {
                        text-decoration: none;
                        color: #ffffff;
                        display: flex;
                        height: 20px;
                        justify-content: center;
                        align-items: center;

                        & > i {
                            font-size: 20px;
                            display: inline-block;
                            margin-left: 15px;


                        }
                    }
                }
            }
            &>.fixed {
                background-color: $brand_grey;
            }

            &>h1 {
                padding: 0;
                margin: 0;
                font-size: 60px;
                font-weight: 800;
                color:#ffffff;
                text-align: center;
            }
            &>p {
                max-width: 700px;
                color: white;
                font-size: 22px;
                font-weight: 400;
                text-align: center;
                line-height: 40px;
                margin-bottom: 70px;
            }
            &>.buttons {
                display: flex;
                flex-direction: row;
                flex-wrap: wrap;
                justify-content: center;
                align-items: center;
                align-content: center;

                &>button {
                    margin: 5px 10px;

                }

            }


            &> .my-nav {
            &>nav {
                position: fixed;
                left: 0;
                top: 0;
                z-index:99;
                width: 100%;
                height: 100%;
                background-color: rgba(59, 67, 76, 0.95);
                display: flex;
                justify-content: flex-start;
                flex-direction: column;
                align-items: center;
                box-sizing: border-box;
                padding: 150px 0;

                &>a {
                    display: block;
                    color:white;
                    font-size: 2em;
                    font-weight: 600;
                    text-decoration: none;
                    padding: 10px 0;
                    transition: all ease 0.2s;

                    &:hover, &.active {
                        color:$brand_orange;
                        transition: all ease 0.2s;
                    }
                }
                &>hr {
                    display: block;
                    width: 170px;
                    height: 0;
                    border: 1px solid $brand_orange;
                    margin: 50px 0;
                }
                &>span {
                    display: flex;
                    flex-direction: row;
                    flex-wrap: wrap;
                    justify-content: center;

                    &>a {
                        margin: 10px 10px;


                        &>i {
                            width: 44px;
                            height: 44px;
                            font-size:1.5em;
                            display: inline-block;
                            text-align: center;
                            line-height: 24px;
                            padding: 9px 0;
                            box-sizing: border-box;


                        }
                    }
                }
                &>:last-child {
                    position: absolute;
                    top: 15px;
                    right: 50px;
                }
            }
            }
        }
    }
</style>


