<template>
    <div>

        <section id="subscribe">

            <input type="text" placeholder="Enter your e-mail address for newsletter">
            <button>SUBSCRIBE</button>
        </section>


    </div>
</template>

<script>
    export default {
        name: "subscribe"
    }
</script>

<style scoped lang="scss">
    @import "../../assets/style/settings";
    #subscribe {
        height: 250px;
        width: 100%;
        background: url("../../assets/images/coffee_bg.jpg") no-repeat center fixed;
        background-size: cover;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        align-items: center;
        align-content: center;

        &>input {
            width: 560px;
            margin: 15px 15px;
            padding: 0 20px;
            box-sizing: border-box;
            height: 50px;
            background-color: #fff;
            border-radius:10px;
            border: none;
            outline: none;


            @include media($small){
                width: 300px;
            }

        }
        &>button {
            background-color: $brand_orange;
            border: none;
            color:white;

            &:hover {
                background-color: white;
                color:$brand_orange;

            }
        }
    }
</style>


