<template>
    <div>

        <section id="works">
            <div class="wr">

                <h2>OUR <span class="orange_color">WORKS</span></h2>
                <p>Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat</p>
                <ul>

                    <li v-for="index in work_step" class="work_view" :class="'work_'+$store.state.random_digits[index-1]" v-in-viewport.once>
                        <div>
                            <h3>ABAZOO WEBSITE</h3>
                            <p>Quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo</p>
                            <button>SHOW PROJECT</button>
                        </div>
                    </li>
                </ul>
                <button @click="add_works">SHOW MORE</button>
            </div>
        </section>
    </div>
</template>

<script>
    export default {
        name: "works",
        data() {
            return {
                works: null,
                work_step:6

            }
        },
        methods: {
            add_works () {
                this.work_step = this.work_step+6;

                for (let i=0; i<6; i++) {
                    this.$store.state.random_digits.push((i+1));
                }

            }
        }

    }
</script>

<style scoped lang="scss">
    @import "../../assets/style/settings";
    #works {
    &>.wr {
         display: flex;
         flex-direction: column;
         justify-content: space-between;
         align-items: center;
         align-content: center;
         padding-top: 100px;
         padding-bottom: 100px;

    &>h2 {
         font-size: 35px;
         font-weight: 600;
     }

    &>p {

         max-width: 450px;
         text-align: center;
     }

    &>ul {
         display: flex;
         flex-direction: row;
         flex-wrap: wrap;
         justify-content: center;

    &>li {
         width: 360px;
         height: 360px;
         border-radius:10px;
         margin: 10px;

    @include media($medium){
    // width: 200px;
    // height: 200px;
    }

    &>div {
         width: 360px;
         height: 360px;
         opacity: 0;
         background-color: rgba(247, 96, 14, 0.9);
         border-radius:10px;
         transition: all 0.2s ease;
         display: flex;
         flex-direction: column;
         justify-content: center;
         align-items: center;
    &:hover {
         opacity: 1;
         transition: all 0.2s ease;
     }

    &>h3 {
         color: white;
         font-size: 25px;
         font-weight: 600;
     }

    &>p {
         max-width: 250px;
         color: white;
         font-size: 14px;
         font-weight: 400;
         line-height: 26px;
         text-align: center;
     }

    &>button {
         background-color: #fff;
         color: $brand_orange;
         border: none;
    &:hover {
         background-color: transparent;
         color: white;
         border: 1px solid white;
     }
    }

    }

    }
    }

    &>button {
         margin: 20px 0;
         background-color: transparent;
         border: 1px solid #d8d9db;
         color: $brand_grey;
    &:hover {
         background-color: $brand_orange;
         color: white;
         border: none;
     }


    }
    }
    }
    .work_view {
        opacity: 0;
        transition: opacity 1.3s;
    }
    .work_view.in-viewport {
        opacity: 1;
    }

    @for $i from 1 through 6 {
        .work_#{$i} {
            background: url("../../assets/images/works_pic_"+$i+".jpg");
        }
    }
</style>


